#gift-cards {
    .g-2 {
        margin-top: 15px;
    }
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 25px;
    }
}
.form-submit {
    .mb-3 {
        display: flex;
        align-items: center;
        margin-top: -15px;

        .btn {
            width: 200px;
        }
        .form-check {
            margin-left: 15px;
        }
    }
}
.alt-container {
    .QnA {
        margin-bottom: 50px;

        div {
            display: flex;
            h5 {
                color: $fern-green;
                margin-right: 5px;
            }
        }
        .row {
            margin-top: 15px;
        }
        hr {
            border-top: 1px dashed black;
        }
    }
    .accordion-button {
        background-color: white;
    }
}