.secondary-navbar {
    padding: 0px;
}
.navbar-text {
    text-wrap: balance;
}
.accordion-button {
    color: rgba(255, 255, 255, 0.75) !important;
    
    &::after {
        filter: grayscale(80%);
    }
}
footer {
    nav{
        @include start-md {
            padding: 0px !important;
        }
    }
    nav:first-child {
        padding-top: 15px !important;

        .navbar-text {
            margin-top: 5px;
            margin-right: 15px;

            @include end-md {
                display: none;
            }
        }
        
    }
    nav:last-child {
        padding-bottom: 15px !important;
    }
    hr {
        width: 100%;
        height: 2px;
        background-color: rgba(255, 255, 255, 0.75) !important;
    }
    .accordion {
        width: 100%;
    }
    .accordion-item, .accordion-button {
        background-color: transparent !important;
    }
    .accordion-item:first-child {
        margin-top: 7px;
    }
    h6 {
        margin-right: 15px;
    }
    .bg-div {
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px -1.95px 2.6px inset, rgba(0, 0, 0, 0.15) 1.95px -1.95px 2.6px;
        position: relative;
        height: 34px;
        width: 100%;
        z-index: 1 !important;
    }
    .icons {
        a{
            font-size: 1.15rem;
        }
    }
}