.fixed-left {
    margin-top: 35px;
    @include boxShadow;

    @include start-md {
        width: 232px;
        flex-flow: column nowrap;
        align-items: flex-start;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin-top: 110px;
    
        .container {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
    
            .navbar-nav {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
        }
    }
}
#basic-navbar-nav {
    @include start-md {
        display: flex;
        flex-direction: column;
    }
}
.menu-accord {
    .accordion-item, .accordion-button {
        background-color: transparent;
        border: 0px;
    }
}
.fixed-right {
    margin-top: 50px;

    @include start-md {
        padding-left: 245px;
        margin-top: 85px;
        margin-bottom: 25px;
    }
    .container {
        display: grid;
        
        margin-top: 25px;
        justify-content: center;
        @include end-md {
            grid-template-columns: auto auto;
        }
        @include start-md {
            grid-template-columns: auto auto auto;
        }
        @include xlg {
            grid-template-columns: auto auto auto auto;
        }

        .card {
            margin: 15px;
            transition: transform 0.15s ease-in-out;

            &:hover{
                transform: scale(1.05);
            }
            @include end-md {
                width: 8rem !important;
            }
            @include start-md {
                width: 10rem !important;
            }
            @include lg {
                width: 12rem !important;
            }
        }
        img {
            width: 150px;
            height: 150px;

            @include end-md {
                margin-left: -12px;
            }
        }
        a {
            text-wrap: balance;
            text-decoration: none;
            text-align: center;
            color: black;
        }
    }
}