$dark: black;
.primary-navbar {
    &.navbar {
        background-color: white;
    }
    .bg-div {
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px inset, rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
        position: absolute;
        margin-top: 110px;
        height: 34px;
        width: 100%;
        z-index: 1 !important;
    }
    .nav-link {
        color: black !important;
        opacity: 0.65;
        transition: opacity 0.15s ease-in-out;
    
        &:hover {
            opacity: 1;
        }
        &.active{
            opacity: 1;
        }
    }
    @include start-md {
        .navbar-nav {
            .nav-link{
                text-wrap: nowrap;
                transition: border-bottom .1s ease-in-out;
                border-bottom: 3px solid transparent;

                &.active{
                    border-bottom: 3px solid $fern-green !important;
                }
            }
        }
    }
}
.user {
    margin: 0px 10px;
    margin-top: 10px;
    @include end-md {
        margin: 10px;
    }
}

    .offcanvas.show {
        form {
            margin-top: 34px;
        }
        .btn {
            margin-top: 15px;
        }

        .navbar-nav {
            .nav-link{
                text-wrap: nowrap;
                transition: font-weight .1s ease-in-out;

                &.active{
                    font-weight: 700;
                }
            }
        }
}
.ico {
    position: relative;
    width: 30px;
    height: 100%;
}
.btn {
    width: 90px;
    margin: 5px;
    border-radius: 50px !important;

    &.btn-outline-dark:hover {
        background-color: rgba(0, 0, 0, 0.1) !important;
        color: $dark !important;
    }
}

@import '~bootstrap/scss/bootstrap';