//===--- Featured Items
.main-carousel {
    .carousel-item{
        height: 60vh;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
        z-index: 1;

        &.carousel-item-next, &.carousel-item-prev {
            .bg-img {
                animation: drop .5s ease-out;
            }
        }
        &:first-child  {
            background-color: #6db580;
            background-image: linear-gradient(45deg, #6DB580 0%, #e0e05f 100%);            
        }
        &:nth-child(2)  {
            background-color: #d08961;
            background-image: linear-gradient(62deg, #D08961 0%, #e5b85c 100%);                  
        }
        &:last-child  {
            background-color: #7d9dc7;
            background-image: linear-gradient(62deg, #7d9dc7 0%, #e495b2 100%);             
        }
        .accordion-item, .accordion-button {
            transition: backdrop-filter 0.35s ease-in-out;
            background-color: transparent !important;
        }
        .accordion-body{
            color: white;
            @include textShadow;
        }
        .ico-btn{
            transition: transform 0.35s ease-in-out;
            
            &.active {
                transform: rotate(-180deg);
            }
        }
        .accordion-button {
            display: flex;
            justify-content: center;

            &::after{
                display: none;
            }
        }
        .bg-img {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
        }
        img {
            @include end-md {
                width: 240px;
                height: 240px;
            }
            width: 360px;
            height: 360px;
            filter: drop-shadow(1.95px 1.95px 2.6px rgba(0, 0, 0, 0.15));
            //filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.35)) drop-shadow(25px 30px 35px rgba(0, 0, 0, 0.35));
        }
    }
    .carousel-control-prev, .carousel-control-next, button {
        filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.24));
    }
    .carousel-control-prev-icon, .carousel-control-next-icon {
        width: 3rem !important;
        height: 3rem !important;

        @include end-md {
            width: 2rem !important;
            height: 2rem !important;
        }
    }
    .accordion-button {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    h2 {
        margin-bottom: 0px !important;
    }
    h2, h6, p {
        color: white !important;
        text-align: center;
        text-wrap: balance;
        @include textShadow;
    }
}

//===--- Mini Menu Items
.head {
    display: flex;
    align-items: end;
    width: 100%;
    height: 40px;

    h3 {
        margin-right: auto;
        margin-bottom: 5px !important;
    }
    p {
        margin-bottom: 5px !important;
    }
    a {
        text-decoration: none;
        color: black;
        opacity: 0.65;
        transition: opacity 0.15s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }
}
hr {
    margin: 0px !important;
}
.mini-carousel {
    display: flex !important;

    .mini-carousel-control-prev-icon, .mini-carousel-control-next-icon {
        font-size: 1.5rem;

        @include end-md {
            font-size: 1rem;
        }
    }
    .mini-carousel-control-prev, .mini-carousel-control-next {
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.5;
        transition: opacity 0.15s ease;
        width: 15%;
        margin: 10px;

        &:hover {
            opacity: $carousel-control-hover-opacity;
        }
    }
    .mini-carousel-items {
        flex-wrap: nowrap !important;
        overflow-x: scroll;
        overflow-y: hidden;
        width: 90%;
        margin-top: 25px;
        scrollbar-width: none;

        .card-body {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .card {
            transition: transform 0.15s ease-in-out;
            background-color: transparent;

            &:hover{
                transform: scale(1.05);
            }
        }
        a {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .mini-carousel-control-prev, .mini-carousel-control-next {
        display: flex;
        cursor: pointer;
    }
    a {
        margin-top: 25px;
        color: #000 !important;
        text-decoration: none !important;
        display: block;
    }
    .card-title {
        text-wrap: balance;
        text-align: center;
    }
}
.menu {
    .mini-carousel-item {
        margin-bottom: -10px;
    }
    img {
        width: 150px;
        height: 150px;
    }
}
//===--- Rewards
.head:nth-child(4) {
    h3, p {
        padding-top: 0px !important; 
    }
    p {
        margin-top: 10px !important;
    }
}
.rewards {
    display: flex;
    align-items: center;
    margin: 25px 0px;
    
    .card:first-child {
        background-color: $fern-green;
        @include boxShadowIn;
    }
    .card:last-child {
        background-color: $fern-green1;
        @include boxShadowIn;
    }
    .card-title, .card-text, .card-link {
        display: flex;
        justify-content: center;
        color: white;
        text-decoration: none;
        @include start-md {
            margin: 5px;
        }
        @include lg {
            margin: 15px;
        }
        @include xlg {
            margin: 25px;
        }
    }
    .card-div {
        margin-left: -1px;
        margin-right: -1px;
        width: 15px;
        height: 22rem;
        clip-path: polygon(0 0, 100% 4.5%, 100% 95.5%, 0% 100%);
        background-color: $fern-green1;
        @include boxShadowIn;
    }
    .btn {
        width: 115px;
        margin: 5px;
        border-radius: 50px !important;
    
        &.btn-outline-light:hover {
            background-color: rgba(0, 0, 0, 0.1) !important;
            color: $light !important;
        }
    }
    &.reverse {
        .card-div {
            clip-path: polygon(0 4.5%, 100% 0, 100% 100%, 0 95.5%);
        }
    }
    .card {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}
.card-1 {
    .card:first-child {
        //background-image: linear-gradient(45deg, rgba(79,121,66,1) 0%, rgba(130,174,115,1) 100%);
        @include boxShadowGreater;
    }
}
.card-2 {
    .card:first-child {
        background-color: #344B46;
    }
    .card:last-child {
        background-color: #13695D;
        //background-image: linear-gradient(315deg, rgba(19,105,93,1) 0%, rgba(80,157,143,1) 100%);
        @include boxShadowGreater;
    }
    .card-div {
        background-color: #344B46;
    }
}
.card-3 {
    .card:first-child {
        background-color: #2F4858;
        //background-image: linear-gradient(45deg, rgba(55,73,85,1) 0%, rgba(103,122,135,1) 100%);
        @include boxShadowGreater;
    }
    .card:last-child {
        background-color: #374955;
    }
    .card-div {
        background-color: #374955;
    }
}
//===--- Gift Cards
.rewards {
    svg {
        width: 8rem;
        height: 8rem;
        color: white;
    }
}
.gift-cards {
    .mini-carousel-items {
        margin-bottom: 25px !important;
    }

}
.head:nth-child(9) {
    h3, p {
        padding-top: 5px !important; 
    }
    p {
        margin-top: 15px !important;
    }
}

//===--- Others
@keyframes drop {
    from {
        transform: translateY(-50px) scale(1.1);
    }
    to {
        transform: translateY(0px) scale(1);
    }
}
.alt-container {
    padding-top: 50px;
    padding-bottom: 50px;
}
.bg-body-tertiary.alt {
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px, rgba(0, 0, 0, 0.15) 1.95px -1.95px 2.6px;
}
@import '~bootstrap/scss/bootstrap';