*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    //font-family: 'Montserrat', sans-serif;
}
html{
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
}
$fern-green: #4F7942;
$fern-green1: #404A3C;

@mixin boxShadow{
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
@mixin boxShadowIn{
    box-shadow: rgba(0, 0, 0, 0.10) 1.95px 1.95px 2.6px inset;
}
@mixin boxShadowGreater{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 19px 38px, rgba(0, 0, 0, 0.27) 0px 15px 12px;
}
@mixin textShadow{
    text-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;
}
@mixin xs{
    @media screen and (max-width: 375px){
        @content;
    }
}
@mixin sm{
    @media screen and (min-width: 576px){
        @content;
    }
}
@mixin start-md{
    @media screen and (min-width: 768px){
        @content;
    }
}
@mixin end-md {
    @media screen and (max-width: 767px){
        @content;
    }
}
@mixin lg{
    @media screen and (min-width: 992px){
        @content;
    }
}
@mixin xlg{
    @media screen and (min-width: 1200px){
        @content;
    }
}
@import './_navBar', './_home', './_menu', './_rewards', './_giftCards', './_account', './_about', './_product';