#account {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    @include end-md {
        margin: 200px 0px;
    }

    h2, p {
        text-align: center;
        text-wrap: balance;
    }
    p:last-child {
        margin-top: 16px;
    }
    .container {
        width: 650px;
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px, rgba(0, 0, 0, 0.15) -1.95px -1.95px 2.6px;
        @include end-md {
            width: 90vw;
        }
    }
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 50px 0px;
        
        .btn {
            width: 200px;
        }
        .form-group {
            width: 400px;
            @include end-md {
                width: 80%;
            }
            margin: 25px 0px;
        }
        .form-submit {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 15px;
            margin-right: 30px;
        }
        .mb-3 {
            margin-bottom: 25px !important;
        }
    }
}