.banner {
    width: 100%;
    height: 60vh;
    background-color: $fern-green;

    .container {
        display: flex;
        align-items: center;
        height: 100%;

        .col {
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 100%;

            @include end-md{
                width: 100px;
            }
        }
    }
    .btn {
        width: 150px;
        margin: 5px;
        border-radius: 50px !important;
    
        &.btn-outline-light:hover {
            background-color: rgba(0, 0, 0, 0.1) !important;
            color: $light !important;
        }
    }
    h2, p {
        text-wrap: balance;
        color: white;
    }
    .ico-reward {
        height: 30vh;
        width: auto;
        color: white;
        

        @include xs{
            height: 10vh;
        }
        @include end-md{
            height: 15vh;
        }
    }
}
.row-items {
    width: 100%;

    h3, p {
        text-align: center;
    }
    p {
        margin-bottom: 25px;
    }
}
#rewards {
    .container {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .bg-body-tertiary {
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px, rgba(0, 0, 0, 0.15) 1.95px -1.95px 2.6px;
    }
    .bg-body {
        background-color: white !important;
    }
    .col:not(.QnA) {
        display: flex;
        justify-content: center;
    }
    .card {
        background-color: transparent;
        text-align: center;
        margin: 25px 0px;
    }
}