#product {
    .banner {
        .container {
            padding: 0px;
        }

        @include lg {
            .container {
                width: 50%;
            }
        }
        @include end-md {
            a {
                width: 100px;
            }
        }
    }
    .alt-container {
        display: flex;
        justify-content: center;
        align-items: start;

        .row {
            width: 400px;
        }
        @include end-md {
            align-items: center;
            flex-direction: column;

            .row {
                width: 100%;
            }
        } 
    }
    .cos {
        margin-top: 50px;

        .form-select {
            margin: 25px 0px;
            padding-bottom: 0px;
            @include start-md {
                width: 50%;
            }
        }
        .sin {
            margin: 50px 0px;
        }
    }
    h2, h6, p {
        text-wrap: balance;
    }
    img {
        @include end-md {
            width: 240px;
            height: 240px;
        }
        @include xs {
            width: 180px;
            height: 180px;
        }
        width: 360px;
        height: 360px;    

        &.other{
            @include end-md {
                height: 160px;
            }
            @include xs {
                height: 120px;
            }
            width: auto;
            height: 300px;    
        }
    }
    .alt-container {
        hr {
            width: 100%;
        }
    }
    form {
        margin: 50px 0px;

        .form-group {

            &:first-child, &:nth-child(2) {
                margin-bottom: 25px;
            }
        }
        .form-group:nth-child(3) {
            display: flex;
            width: 100%;
            .form-select{
                width: 200px;
                margin-right: 25px;
                margin-top: 0px;
            }
            @include end-md {
                flex-direction: column;
                .form-select{
                    width: 50%;
                }
            }
        }
        .btn {
            margin-right: 15px;
            width: 200px;
        }
    }
}